import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { ajaxOperations } from '../utils/ajax';
import { config } from '../utils/config';
import { DashBoardRoutes } from '../utils/dashboardroutes';
export const DashBoard = () => {
  const [menus, setMenus] = useState([]);
  useEffect(() => {
    const promise = ajaxOperations.fetch(config.URLS.MENU_FETCH_URL);
    promise.then(response => {
      console.log('Response is ', response.data);
      setMenus(response.data.menus);
    }).catch(err => {
      console.log('Dashboard Menu Fetch Error ', err);
    })
  }, []);

  return (
    <>
      <header className="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 shadow">
        <a className="navbar-brand col-md-3 col-lg-2 me-0 px-3" href="#">Test Engine Project</a>
        <button className="navbar-toggler position-absolute d-md-none collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#sidebarMenu" aria-controls="sidebarMenu" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <input className="form-control form-control-dark w-100" type="text" placeholder="Search" aria-label="Search" />
        <ul className="navbar-nav px-3">
          <li className="nav-item text-nowrap">
            <a className="nav-link" href="#">Sign out</a>
          </li>
        </ul>
      </header>

      <div className="container-fluid">
        <div className="row">
          <nav id="sidebarMenu" className="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse">
            <div className="position-sticky pt-3">
              <ul className="nav flex-column">

                {menus.map(menu => {
                  return (<li className="nav-item">
                    <NavLink className="nav-link active" to={menu.link}>
                      <span data-feather="home"></span>
                      {menu.name}
                    </NavLink>
                  </li>);
                })}

                <li className="nav-item">
                  <a className="nav-link active" aria-current="page" href="#">
                    <span data-feather="home"></span>
              All Test
            </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">
                    <span data-feather="file"></span>
              Test Assign
            </a>
                </li>

                <li className="nav-item">
                  <a className="nav-link" href="#">
                    <span data-feather="file"></span>
              Score Card
            </a>
                </li>

              </ul>

            </div>
          </nav>

          <main className="col-md-9 ms-sm-auto col-lg-10 px-md-4">
            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
              <h1 className="h2">Welcome UserName</h1>
              

              {/* Header Close Here{} */}
            </div>

            <DashBoardRoutes />


          </main>
        </div>
      </div>
    </>
  );

}